import request from '../utils/request'

// {articleId: ?}
export function getCommentByArticleId (params) {
  return request({
    method: 'GET',
    url: '/getComment',
    params
  })
}

// {parentId: ?,user_name: ?,email: ?,content: ?,article_id: ?}
export function saveComment (params) {
  return request({
    method: 'post',
    url: '/saveComment',
    data: params
  })
}



