<script>

import { getBasicInfo,updateBasicInfo,updatePassword } from '../api/HelloWorld.js'
import { getPicturePageAndLimit, getPictureFile } from '../api/picture.js'
import { getAICourseArticle, getCodeCourseArticle } from '../api/article.js'
import { getCommentByArticleId, saveComment } from '../api/comment.js'
import { doRegister,doSendCode } from '../api/login.js'
import MD5 from 'blueimp-md5'
import 'github-markdown-css'
export default {
  data () {
    return {
      input: '',
      form: {
        user_name: '',
        email: '',
        content: '',
      },
      loginForm:{
        email:'',
        code:'',
        password:'',
      },
      rules: {
        email: [
          {  required: true, message: '请输入邮箱', trigger: 'change' }
        ],
        code: [
          {  required: true, message: '请输入验证码', trigger: 'change' }
        ],
        password: [
          {  required: true, message: '请输入密码', trigger: 'change' }
        ]
      },
      imgListList: [],
      imgss: [],
      downloadTitle: '下载',
      activeIndex: '图片',
      isloading: false,
      imgNum: 1,
      imgListNum: 3,
      page: 1,
      limit: 3,
      htmlMD: {},
      isComment: false,
      commentList: [],
      AiCourseList: [],
      codeCourseList: [],
      commentParentId: -1,
      commentName: '',
      dialogFormVisible:false,
      isLogin:true,
      countDownText:'发送',
      countDownNum:30,
      countDownId:'',
      isCoutDown:false,
      //是否已登录
      alLogin:true,
      //我的个人信息对话框
      myInfoOpen:false,
      //基本资料表单
      basicInfoForm: {},
      //基本资料表单验证
      basicInfoRules: {
        nickName: [
          {  required: true, message: '昵称不能为空', trigger: ['change','blur'] }
        ],
      },
      //修改密码表单
      updatePasswordForm: {},
      //修改密码表单验证
      updatePasswordRules: {
        oldPassword: [
          {  required: true, message: '旧密码不能为空', trigger: ['change','blur'] }
        ],
        newPassword: [
          {  required: true, message: '新密码不能为空', trigger: ['change','blur'] }
        ],
        confirmPassword: [
          {  required: true, message: '确认密码不能为空', trigger: ['change','blur'] }
        ],
      },
    }
  },
  methods: {
    updatePassword(){
      var _this = this;
      _this.$refs['updatePasswordForm'].validate((valid) => {
        if (valid) {
          updatePassword(_this.updatePasswordForm).then(response => {
            if (response.isError){//如果保存失败
              _this.$message({
                message: response.msg,
                type: 'danger'
              });
            }else {
              _this.$message({
                message: response.msg,
                type: 'success'
              });
            }
          })
        } else {
          return false
        }
      })
    },
    saveBasicInfo(){
      var _this = this;
      _this.$refs['basicInfoForm'].validate((valid) => {
        if (valid) {
          updateBasicInfo(_this.basicInfoForm).then(response => {
            if (response.isError){//如果保存失败
              _this.$message({
                message: response.msg,
                type: 'danger'
              });
            }else {
              _this.$message({
                message: response.msg,
                type: 'success'
              });
            }
          })
        } else {
          return false;
        }
      });
    },
    clearFormInfo(){
      this.myInfoOpen = false
      this.basicInfoForm = {};
      this.updatePasswordForm = {};
      this.$refs['basicInfoForm'].resetFields()
      this.$refs['updatePasswordForm'].resetFields()
    },
    openMyInfo(){
      this.myInfoOpen = true;
      getBasicInfo().then(response => {
        this.basicInfoForm = response;
      })
    },
    loginPageShow(){
      this.loginForm={}
      this.dialogFormVisible = true
    },
    countDown(){
      this.countDownNum = this.countDownNum - 1
      this.countDownText = '重新发送('+this.countDownNum+')'
      if(this.countDownNum == 0){
        this.isCoutDown = false
        this.countDownText = '重新发送'
        clearInterval(this.countDownId);
      }
    },
    loginSendCode(){
      let reg = /^[a-zA-Z0-9]+([-_.][A-Za-zd]+)*@([a-zA-Z0-9]+[-.])+[A-Za-zd]{2,5}$/
      if(reg.test(this.loginForm.email)){
        doSendCode({email: this.loginForm.email})
        this.countDownText = '重新发送(30)'
        this.isCoutDown = true
        this.countDownId = setInterval(this.countDown, 1000);
      }else{
        const h = this.$createElement;
        this.$message({
          message: h('p', null, [
            h('i', { style: 'color: red' }, '邮箱输入错误')
          ])
        });
      }
    },
    loginSubmit(){
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          clearInterval(this.countDownId);
          this.countDownText = '发送'
          doRegister({email: this.loginForm.email, code: this.loginForm.code, password: MD5(this.loginForm.password)}).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.dialogFormVisible = false
            }
          })
        } else {
          return false;
        }
      });

    },
    onSubmit () {
      var tempForm = this.form
      if (tempForm.user_name !== '' && tempForm.email !== '' && tempForm.content !== '') {
        if (tempForm.content.indexOf('@' + this.commentName + ':') !== -1) {
          tempForm.content = tempForm.content.substring(this.commentName.length + 4)
        } else {
          this.commentParentId = -1
        }
        tempForm.parent_id = this.commentParentId
        tempForm.article_id = this.activeIndex.substring(1)
        saveComment(tempForm).then(() => {
          // 更新评论
          this.form = this.$options.data().form
          getCommentByArticleId({article_id: this.activeIndex.substring(1)}).then((response) => {
            this.commentList = response.data
          })
        })
      }
    },
    onReComment (comment) {
      this.commentParentId = comment.id
      this.commentName = comment.name
      this.form.content = '@' + comment.name + ':'
      this.$refs.articleScoll[0].scrollTop = this.$refs.articleScoll[0].scrollHeight - this.$refs.articleScoll[0].clientHeight
    },
    onReset () {
      console.log('重置')
    },
    handleSelect (key, keyPath) {
      if (key.substring(0, 1) === ':') {
        this.isComment = true
        getCommentByArticleId({article_id: key.substring(1)}).then((response) => {
          this.commentList = response.data
        })
      } else {
        this.isComment = false
      }
      this.activeIndex = key
    },
    download (index) {
      getPictureFile(index).then((response) => {
        const blob = new Blob([response.data])
        const elink = document.createElement('a')
        elink.download = index.substring(index.lastIndexOf('/'))
        elink.style.display = 'none'
        elink.href = URL.createObjectURL(blob)
        document.body.appendChild(elink)
        elink.click()
        URL.revokeObjectURL(elink.href) // 释放URL 对象
        document.body.removeChild(elink)
      }).catch((error) => {
        this.$message({
          message: error
        })
      })
    },
    onScroll (event) {
      if ((event.target.scrollHeight - event.target.clientHeight) - 30 < event.target.scrollTop) {
        if (!this.isloading) {
          this.getPicture()
        }
      }
      if ((event.target.scrollHeight - event.target.clientHeight) * 0.6 > event.target.scrollTop) {
        this.isloading = false
      }
    },
    getPicture (isForce) {
      if (!this.isloading || isForce) {
        this.isloading = true
        return getPicturePageAndLimit({'page': this.page, 'limit': this.limit}).then((response) => {
          var data = response.data
          for (const iterator of data) {
            console.log(this.imgListNum)
            if (this.imgNum % this.imgListNum !== 0) {
              iterator.name = this.imgNum
              this.imgListList[(this.imgNum % this.imgListNum) - 1].list.push(iterator)
            } else {
              iterator.name = this.imgNum
              this.imgListList[this.imgListNum - 1].list.push(iterator)
            }
            this.imgss.push(iterator.url)
            this.imgNum++
          }
          this.isloading = false
          this.page++
          console.log(this.imgListList)
        })
      }
    }
  },
  created () {
    for (let index = 0; index < this.imgListNum; index++) {
      this.imgListList.push({name: '/' + index, list: []})
    }
  },
  mounted () {
//     console.log(Live2DCubismCore)
//     const cubism2Model =
//   "https://cdn.jsdelivr.net/gh/guansss/pixi-live2d-display/test/assets/shizuku/shizuku.model.json";
// const cubism4Model =
//   "https://cdn.jsdelivr.net/gh/guansss/pixi-live2d-display/test/assets/haru/haru_greeter_t03.model3.json";
//     (async function main() {
//       const app = new PIXI.Application({
//         view: document.getElementById("canvas"),
//         autoStart: true,
//         resizeTo: window
//       });
//       const model2 = await Live2DModel.from(cubism2Model);
//       const model4 = await Live2DModel.from(cubism4Model);

//       app.stage.addChild(model2);
//       app.stage.addChild(model4);

//       model2.scale.set(0.3);
//       model4.scale.set(0.25);

//       model4.x = 300;
//     })()
    // this.imgListList[0].list.push({name: '1', url: require('../img/1.png')})
    // this.imgListList[0].list.push({name: '2', url: require('../img/2.png')})
    // this.imgListList[0].list.push({name: '3', url: require('../img/3.png')})
    // this.imgListList[1].list.push({name: '4', url: require('../img/4.png')})
    // this.imgListList[1].list.push({name: '5', url: require('../img/5.png')})
    // this.imgListList[1].list.push({name: '6', url: require('../img/6.png')})
    // this.imgListList[2].list.push({name: '7', url: require('../img/7.png')})
    // this.imgListList[2].list.push({name: '8', url: require('../img/8.png')})
    // this.imgListList[2].list.push({name: '9', url: require('../img/9.png')})

    // this.imgss.push(require('../img/1.png'))
    // this.imgss.push(require('../img/2.png'))
    // this.imgss.push(require('../img/3.png'))
    // this.imgss.push(require('../img/4.png'))
    // this.imgss.push(require('../img/5.png'))
    // this.imgss.push(require('../img/6.png'))
    // this.imgss.push(require('../img/7.png'))
    // this.imgss.push(require('../img/8.png'))
    // this.imgss.push(require('../img/9.png'))
    // this.getPicture().catch(() => {
    //
    // }).finally(() => {
    //   this.getPicture().finally(() => {
    //     this.getPicture()
    //   })
    // })
    this.AiCourseList = [{id: '1', name: 'Stable Diffusion AI教程', url: ''}, {id: '2', name: 'Stable Diffusion AI教程', url: require(`../img/stable-diffucsion1.md`)}]
    this.codeCourseList = [{id: '3', name: '编程教程1', url: require(`../img/stable-diffucsion1.md`)}, {id: '4', name: '编程教程2', url: require(`../img/stable-diffucsion1.md`)}]
    getAICourseArticle().then((response) => {
      this.AiCourseList = response.data
    })
    getCodeCourseArticle().then((response) => {
      this.codeCourseList = response.data
    })
    this.commentList.push({id: 1, name: '龙羊峡中扬', content: 'Stable Diffusion 支持txt2img(文生图)和img2img（图生图）。一般用文生成图。想要生成比较好看的图像还是不容易的，需要选到好的模型和对应的prompt。需要多多学习和实验，最终成为魔法咒语大师(笑)。', child: [{id: 7, name: '飞天小堇瓜', content: '都是骗人的，别信', child: []}, {id: 8, name: '红豆糯米团er', content: '确实', child: []}]})
    this.commentList.push({id: 2, name: '飞天咸鱼', content: 'Stable Diffusion 是根据模型生成的，不同的模型对同一prompt生成的图片风格和内容都不一样。有一些针对真人的，有一些是生成二次元的，有一些是专门生成se图的。', child: [{id: 3, name: '三猫一汪的日常', content: '什么？', childList: []}]})
    this.commentList.push({id: 4, name: '跳跳茄', content: '针对真人的(笑)。', child: []})
  }
}
</script>

