import request from '../utils/request'

// params = {id : ?}
export function getArticleById (params) {
  return request({
    method: 'GET',
    url: '/ai-article',
    params
  })
}

export function getAICourseArticle () {
  return request({
    method: 'GET',
    url: '/ai-AiCourse'
  })
}

export function getCodeCourseArticle () {
  return request({
    method: 'GET',
    url: '/ai-CodeCourse'
  })
}



