import request from '../utils/request'

// 获取基本资料信息
export function getBasicInfo () {
  return request({
    method: 'GEt',
    url: '/getBasicInfo',
  })
}

// 修改基本资料信息
export function updateBasicInfo (data) {
  return request({
    method: 'POST',
    url: '/updateBasicInfo',
    data: data
  })
}

// 修改密码
export function updatePassword (data) {
  return request({
    method: 'POST',
    url: '/updatePassword',
    data: data
  })
}



