import request from '../utils/request'

// params = {email : ?, password : ?}
export function doRegister (params) {
  return request({
    method: 'POST',
    url: '/register',
    params
  })
}
// params = {email : ?}
export function doSendCode (params) {
    return request({
        method: 'POST',
        url: '/send-code',
        params
    })
}


