import request from '../utils/request'

// params = {page : ?, limit : ?}
export function getPicturePageAndLimit (params) {
  return request({
    method: 'GET',
    url: '/ai-images',
    params
  })
}

export function getPictureFile (url) {
  return request({
    method: 'GET',
    responseType: 'blob',
    url: url
  })
}



